@import "./null.css";


.root {
	display: flex;
	justify-content: center;
	align-items: center;
}

.game {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	margin: 50px 0px 0px 0px;
	font-size: 2rem;
}

.game__title {
	margin: 0px 0px 20px 0px;
}

.game__description {
	font-size: 1.5rem;
	margin: 0px 0px 20px 0px;
}

.board {
	display: flex;
	justify-content: space-around;
	align-items: center;
	flex-wrap: wrap;
	width: 40vw;
	height: 40vw;
}

.board__row {
	flex: 0 0 40vw;
	display: flex;
	justify-content: space-around;
	align-items: center;
}

.card {
	display: flex;
	justify-content: center;
	align-items: center;
	flex: 0 0 10vw;
	height: 10vw;
	border: 1px solid black;
}

.cardHidden {
	color: white;
}