@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
* {
	margin: 0;
	padding: 0;
	border: 0;
}

*,
*::before,
*::after {
	box-sizing: border-box;
}

:focus,
:active {
	outline: none;
}

a:focus,
a:active {
	outline: none;
}

nav,
footer,
header,
aside,
section {
	display: block;
}

html,
body {
	height: 100%;
	width: 100%;
	font-size: 100%;
	line-height: 1;
	font-size: 16px;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
	font-family: 'Roboto', sans-serif;
	color: black;
}

input,
button,
textarea {
	font-family: 'Roboto', sans-serif;
}

input::-ms-clear {
	display: none;
}

button {
	cursor: pointer;
}

button::-moz-focus-inner {
	padding: 0;
	border: 0;
}

a,
a:visited {
	text-decoration: none;
}

a:hover {
	text-decoration: none;
}

ul li {
	list-style: none;
}

img {
	vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-size: inherit;
	font-weight: 400;
}
.root {
	display: flex;
	justify-content: center;
	align-items: center;
}

.game {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	margin: 50px 0px 0px 0px;
	font-size: 2rem;
}

.game__title {
	margin: 0px 0px 20px 0px;
}

.game__description {
	font-size: 1.5rem;
	margin: 0px 0px 20px 0px;
}

.board {
	display: flex;
	justify-content: space-around;
	align-items: center;
	flex-wrap: wrap;
	width: 40vw;
	height: 40vw;
}

.board__row {
	flex: 0 0 40vw;
	display: flex;
	justify-content: space-around;
	align-items: center;
}

.card {
	display: flex;
	justify-content: center;
	align-items: center;
	flex: 0 0 10vw;
	height: 10vw;
	border: 1px solid black;
}

.cardHidden {
	color: white;
}
